import { Component, OnInit } from '@angular/core';
import AOS from 'aos';

@Component({
  selector: 'app-tuja',
  templateUrl: './tuja.component.html',
  styleUrls: ['./tuja.component.css']
})
export class TujaComponent implements OnInit {

  constructor() { }

  ngOnInit(): void {
    AOS.init(
      {
        duration: 700,
        delay: 400,
        once: true,
      }
    );
    AOS.refresh(
      {
        delay: 400,
        once: true,
      }
    )


  }




}
